    /*animate the background gradient position, use vh so that start/end syncs up viewport horizontal*/
    @keyframes rainbow {
        from {background-position: -100vh 0}
        to {background-position: 100vh 0}
    } 
    
    /*compatibility*/
    @-webkit-keyframes rainbow {
        from {background-position: -100vh 0}
        to {background-position: 100vh 0}
    }
    
     .rainbow {
      border-right: double 0.3em transparent;
      border-radius: 10px;
         /*added a colourstop here, without the third colourstop you get a hard edge*/
      background: linear-gradient(black, black), 
      repeating-linear-gradient(to right,    
      hsl(224, 85%, 66%),
      hsl(256, 85%, 66%),
      hsl(269, 85%, 66%),
      hsl(314, 85%, 66%),
      hsl(359, 85%, 66%),
      hsl(44, 85%, 66%),
      hsl(89, 85%, 66%),
      hsl(134, 85%, 66%),
      hsl(179, 85%, 66%),
      hsl(202, 85%, 66%));
      background-origin: border-box;
      background-clip: content-box, border-box;
    
    
      -webkit-animation: 20s linear 1s infinite alternate rainbow;
    
    
              animation: 20s linear 1s infinite alternate rainbow;
